.card-container {
    display: flex;
    flex-direction: column;
    background-color: #ada;
    border: 1px solid grey;
    border-radius: 10px;
    padding: 20px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform .5s ease-out;
}
.card-container:hover {
    transform: scale(1.02)
}